.modal-wrap{
   width: 100%;
   height: 100%;
   min-height:100%;
   position: fixed;
   top:0;
   left:0;
   margin:0;
   padding:0;
   z-index: 10;
}

.msg-modal-backdrop{
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0px;
   left: 0px;
   background: rgba(0, 0, 0, 0.6);
}



