.signin-panel{
   background-color: white;
   position: absolute;
   padding:1rem;
   font-size: 1.25rem;
   top: 50%;
   left: 50%;
   -webkit-transform: translate(-50%, -50%);
       -ms-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
   text-align: left;
   border: 5px solid #2E3409;
   border-radius: 3px;

}

.signin-panel .social-logins{
  text-align: center;
}

.social-logins h3{
  margin: 5px;
}

hr{
  margin-bottom: 5px;
}



